<script>
  import { List } from "@ui/index";
  import { MeleeWeapon } from "@ui/index";

  export let entity = null;

  $: weapons = [...($entity.weapons || [])].filter(
    (weapon) => weapon.getType() === "melee_weapon"
  );
</script>

<style>
  

/*# sourceMappingURL=data:application/json;base64,eyJ2ZXJzaW9uIjozLCJzb3VyY2VzIjpbXSwibmFtZXMiOltdLCJtYXBwaW5ncyI6IiIsImZpbGUiOiJzcmNcXHVpXFxjb21wb25lbnRzXFxlZGl0b3JzXFxwYW5lbHNcXE1lbGVlV2VhcG9ucy5zdmVsdGUifQ== */</style>

<List
  list={weapons}
  title="Melee Weapons"
  component={MeleeWeapon}
  on:additem={() => $entity.addWeapon('melee_weapon')}>
  <tr slot="header">
    <th class="w-full">Melee Weapons</th>
    <th>Usage</th>
    <th>Lvl</th>
    <th>Parry</th>
    <th>Block</th>
    <th>Damage</th>
    <th>Reach</th>
    <th>ST</th>
  </tr>
</List>
